html, body {
    overflow-x: hidden;
}

.scrollbar-hidden::-webkit-scrollbar {
    display: none;
  }
  
  /* Hide scrollbar for IE, Edge add Firefox */
  .scrollbar-hidden {
    -ms-overflow-style: none;
    scrollbar-width: none; /* Firefox */
  }

/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

.videoContainer{
    aspect-ratio: 16 / 9;
    width: 100%;
}

.chatContainer{
    position: absolute;
    width: 15%;
    height: 100%;
    bottom: 0px;
    right: -15%;
    z-index: 10;
    background:rgba(0, 0, 0, 0.7);
}

.container{
    width: 100%;
    height: 100%;
    position: relative;
}

.messagesContainer{
    position: absolute;
    width: 100%;
    height: 75%;
    top: 0px;
    overflow-y:scroll;
}

.messagesContainer h1{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    padding-left: 10px;
    color: white;
}

.userDataContainer{
    position: absolute;
    width: 100%;
    height: 25%;
    bottom: 0px;
}

.userDataContainer form{
    width: 100%;
    height: 100%;
    position: relative;
}

.name-field{
    width: 100%;
    height: 25%;
    position: absolute;
    text-align: center;
    padding-top: 5px;
    top: 0px;
}

.name-field input{
    position: relative;
    width: 90%;
    height: 25px;
    border-radius: 5px;
    border-color: darkgray;
}

.message-field{
    width: 100%;
    height: 75%;
    position: absolute;
    text-align: center;
    padding-top: 5px;
    bottom: 0px;
}

.message-field input{
    position: relative;
    width: 90%;
    height: 80px;
    border-radius: 5px;
    border-color: darkgray;
    resize: none;
}

.userDataContainer form .btnForm{
    width: 90%;
    height: 35px;
    position: absolute;
    bottom: 5px;
    margin: 0px auto;
    left: 50%;
    transform: translate(-50%, 0%);
    -ms-transform: translate(-50%, 0%); /* for IE 9 */
    -webkit-transform: translate(-50%, 0%); /* for Safari */
}

.messageItem{
    margin: 5px;
    padding: 1px;
    font-family: arial;
    font-size: 13px;
    color: white;
    text-align: center;
}

.messageItem b{
    color: rgb(255, 255, 255);
}

.btnChat{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border: none;
    width: 80px;
    height: 40px;
    background-color: darkred;
    position: absolute;
    top: 30px;
    z-index: 100;
    left: -80px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 13px;
    text-align: center;
    color:white;
    padding-top: 5px;
    padding-bottom: 5px;
    cursor: pointer;
    border-radius: 10px 0px 0px 10px;
}

.local{
    background-color: rgba(252, 0, 0, 0.7);
    border-radius: 15px;
    margin-left: 40px;
    padding:2px 5px 2px 5px;
}

.client{
    background-color: rgba(102, 102, 102, 0.7);
    border-radius: 15px;
    margin-right: 40px;
    padding:2px 5px 2px 5px;
}

